@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;700&display=swap");

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: "Inter", sans-serif;
}

/* NAVBAR STYLES */

.nav--bar {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 25px 0;
  background-color: #84a6f1;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  margin-bottom: 20px;
}

.nav--logo {
  width: 55px;
  margin-right: 15px;
}

.nav--h1 {
  font-size: 2rem;
  color: #fff;
}

/* CARD STYLES */

.section--card {
  display: flex;
}

.section--image {
  float: left;
  max-width: 400px;
  max-height: 400px;
  margin: 35px;
  border-radius: 20px;
}

.section--description {
  margin: auto;
  padding-right: 50px;
}

.pin {
  width: 20px;
  height: 20px;
}

.section--h3 {
  display: inline;
  color: rgb(83, 83, 83);
  letter-spacing: 1px;
  font-size: 1rem;
}

.section--a {
  font-size: 0.8rem;
  margin-left: 20px;
  color: rgb(165, 165, 165);
}

.section--h1 {
  font-size: 2.2rem;
  padding: 15px 0;
}

.section--h5 {
  padding: 10px 0;
}

.section--p {
  color: rgb(83, 83, 83);
}

/* FOOTER STYLES */

.footer {
  background-color: #84a6f1;
  padding: 20px 0;
  text-align: center;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.fa {
  color: #fff;
  text-decoration: none;
  padding: 10px;
  font-size: 2rem;
}

.fa-facebook:hover {
  color: #3b5998;
}

.fa-linkedin:hover {
  color: springgreen;
}

.fa-instagram:hover {
  color: #f77737;
}

.fa-github:hover {
  color: purple;
}

.footer--p {
  font-size: 0.7rem;
  color: rgb(255, 255, 255);
}
